<template>
  <v-ons-page>
    <v-ons-toolbar modifier="transparent"></v-ons-toolbar>

    <v-ons-list>
      <v-ons-list-item modifier="chevron" v-for="lang in languages" :key="lang.code" @click="setLanguage(lang.code)">
        <div class="center">{{ lang.name }}</div>
      </v-ons-list-item>
    </v-ons-list>
  </v-ons-page>
</template>

<script>
export default {
  name: 'menu',
  data () {
    return {
      'languages': [
        {
          code: 'nb',
          name: 'Norsk'
        },
        {
          code: 'en',
          name: 'English'
        }
      ]
    }
  },
  methods: {
    setLanguage: function (code) {
      this.$store.commit('homepage/setLanguage', code)
      this.$store.commit('splitter/toggle', false)
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.header {
  text-align: center;
  margin-bottom: 20px;
}

img {
  max-width: 150px;
}

ons-list-title {
  text-transform: none;
}

ons-list-item {
  cursor: pointer;
}
</style>



