<template>
  <v-ons-page>
    <v-ons-toolbar class="home-toolbar">
      <div class="left">
        <v-ons-toolbar-button @click="$store.commit('splitter/toggle')">
          <v-ons-icon icon="fa-bars"></v-ons-icon>
        </v-ons-toolbar-button>
      </div>
      <div class="center">{{ msg[language] }}</div>
      <div v-if="allowInstall" class="right">
        <v-ons-toolbar-button id="installbutton" @click="install()">
          +
        </v-ons-toolbar-button>
      </div>
    </v-ons-toolbar>

    <v-ons-list>
      <v-ons-list-header>
        <div class="center">
        {{ from_lang[language] }} - {{ to_lang[language] }}
        </div>
      </v-ons-list-header>
      <v-ons-list-item v-for="item in words" :key="item.link">
        <div class="left">{{ item[language] }}</div>
        <div class="right">{{ item.az }}</div>
      </v-ons-list-item>
    </v-ons-list>

  </v-ons-page>
</template>

<script>
export default {
  name: 'home',
  computed: {
    language: function () {
      return this.$store.state.homepage.language
    }
  },
  data () {
    return {
      msg: {nb: 'Ordliste for barnehjemmet', en: 'Childrens Home Dictionary'},
      to_lang: {nb: 'Aserbajdsjansk', en: 'Azeri'},
      from_lang: {nb: 'Norsk', en: 'English'},
      allowInstall: false,
      words: [
        {az: 'diş fırçası', nb: 'tannbørst', en: 'toothbrush'},
        {az: 'sabun', nb: 'såpe', en: 'soap'},
        {az: 'diş pastası', nb: 'tannkrem', en: 'toothpaste'},
        {az: 'dəsmal', nb: 'håndduk', en: 'towel'},
        {az: 'pampers', nb: 'bleie', en: 'dipers'},
        {az: 'paltar', nb: 'klesplagg', en: 'cloths'},
        {az: 'kalyeska', nb: 'rullestol', en: 'wheelchair'},
        {az: 'yemək', nb: 'mat/å spise', en: 'food/eat'},
        {az: 'yaş salfetka', nb: 'våtserviett', en: 'wet wipe'},
        {az: 'qaşıq', nb: 'skje', en: 'spoon'},
        {az: 'gəl', nb: 'kom', en: 'come'},
        {az: 'get', nb: 'gå', en: 'go/leave'},
        {az: 'dayan', nb: 'stopp', en: 'stop'},
        {az: 'vurma', nb: 'ikke slå', en: 'don\'t hit'},
        {az: 'itələmə', nb: 'ikke dytt', en: 'don\'t push'},
        {az: 'olmaz', nb: '"slik gjør man ikke"', en: 'that is not allowed'},
        {az: 'Nə qəşəng', nb: 'Så fin', en: 'How pretty'},
        {az: 'afərin', nb: 'kjempebra', en: 'great work'},
        {az: 'əla', nb: 'supert', en: 'super'},
        {az: 'bir az gözlə', nb: 'vent litt', en: 'wait a bit'},
        {az: 'Bax', nb: 'se!', en: 'look!'},
        {az: '(məni) burax', nb: 'slipp (meg)', en: 'release (me)'},
        {az: 'götür', nb: 'plukk opp', en: 'pick up'},
        {az: 'apar', nb: 'ta med', en: 'take away'},
        {az: 'O batırıb', nb: 'han/hun har bæsja', en: 'he/she has pooped'},
        {az: 'Kim qalıb?', nb: 'Hvem gjenstår?', en: 'Who remains?'},
        {az: 'Kim yeyib?', nb: 'Hvem har spist?', en: 'Who has eaten?'},
        {az: 'O yeyib', nb: 'Han/hun har spist', en: 'He/She has eaten'},
        {az: 'O yeməyib', nb: 'Han/hun har ikke spist', en: 'He/She hasn\'t eaten'},
        {az: 'vsyo', nb: 'ferdig/det var det/det var alt', en: 'finished/that is all'},
        {az: '(...) istəyirsən?', nb: 'Vil du (ha ...)?', en: 'Do you want (...)'},
        {az: 'stul', nb: 'stol', en: 'chair'},
        {az: 'dur', nb: 'stå', en: 'stand'},
        {az: 'otur', nb: 'sitt', en: 'sit'},
        {az: 'həyətə', nb: 'ut (om å gå ut etc) (egentlig "til gårdsplassen")', en: 'outside'}
      ]
    }
  },
  methods: {
    install () {
      // hide our user interface that shows our A2HS button
      this.allowInstall = false
      // Show the prompt
      window.deferredPrompt.prompt()
      // Wait for the user to respond to the prompt
      window.deferredPrompt.userChoice
        .then((choiceResult) => {
          if (choiceResult.outcome === 'accepted') {
            console.log('User accepted the A2HS prompt')
          } else {
            console.log('User dismissed the A2HS prompt')
          }
          window.deferredPrompt = null
        })
    }
  },
  mounted () {
    console.log('Installing event handler')
    window.addEventListener('beforeinstallprompt', (e) => {
      console.log('Got beforeinstallprompt event!')
      // Prevent Chrome 67 and earlier from automatically showing the prompt
      e.preventDefault()
      // Stash the event so it can be triggered later.
      window.deferredPrompt = e
      // Update UI notify the user they can add to home screen
      this.allowInstall = true
    })
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.header {
  text-align: center;
}

img {
  max-width: 300px;
}

ons-list-title {
  text-transform: none;
}

ons-list-title:not(:first-of-type) {
  margin-top: 30px;
}

ons-card {
  text-align: center;
}

ons-list-item, ons-card {
  cursor: pointer;
}
</style>



